import React from "react";
import { useEffect, useState } from "react";
import "../App.css";
import styled from "styled-components";
//import { Link } from "react-router-dom";
import { Preloader } from "../components/preloader";

export function Header() {
  const [done, setDone] = useState(undefined);

  useEffect(() => {
    setTimeout(() => {
      setDone(true);
    }, 3000);
  }, []);

  return !done ? (
    <header class="container loader">
      <Preloader />
    </header>
  ) : (
    <header></header>
  );
}

export function Body() {
  return <div class="body"></div>;
}

export function Footer() {
  return (
    <FooterContainer class="main-footer">
      <div class="middle">
        <div class="container">
          <div class="row">
            {/* Column 1 */}
            <div class="col-md-3 col-sm-6">
              <h4>Helpful links</h4>
              <li class="quick-links">Support</li>
              <li class="quick-links">Festival</li>
              <li class="quick-links">News</li>
              <li class="quick-links">Jobs</li>
              <li class="quick-links">Blog</li>
            </div>
            {/* Column 2 */}
            <div class="col-md-3 col-sm-6">
              <h4>Others</h4>
              <li class="quick-links">Privacy policy</li>
              <li class="quick-links">Security</li>
              <li class="quick-links">Terms of use</li>
            </div>
          </div>
          <div class="footer-bottom">
            <p class="text-xs-center">
              &copy;{new Date().getFullYear()} Modern Music
            </p>
          </div>
        </div>
      </div>
    </FooterContainer>
  );
}

const FooterContainer = styled.footer`
  .middle {
    background: #232;
    padding-top: 3rem;
    padding-bottom: 2rem;
    color: white;
  }

  .quick-links {
    color: #bbb;
    text-align: left;
    &:hover {
      color: white;
    }
  }
`;
