import React from "react";
import { Header } from "./main";
//import { Body } from "./main";
import { Footer } from "./main";
import { AddTickets } from "../components/addTickets";

export function Tickets() {
  return (
    <div class="container tickets">
      <Header />

      <div class="row tables">
        <div class="col">
          <div style={{ display: "block", padding: 30 }}>
            <h4 class="bg-dark text-white">Iron Filings and a Compass</h4>
            <AddTickets />
          </div>
          <div style={{ display: "block", padding: 30 }}>
            <h4 class="bg-dark text-white">No Rest For the Rappers</h4>
            <AddTickets />
          </div>
          <div style={{ display: "block", padding: 30 }}>
            <h4 class="bg-dark text-white">McPineapple</h4>
            <AddTickets />
          </div>
        </div>
        <div class="col mt-7">
          <button class="btn-dark btn-lg mt-3"> Buy Ticket(s) Now! </button>
        </div>
      </div>

      <Footer />
    </div>
  );
}
