import React from "react";
import { Nav, Navbar } from "react-bootstrap";
import styled from "styled-components";

const Styles = styled.div`
  .navbar {
    background-color: #232;
  }

  .navbar-brand,
  .navbar-nav .nav-link {
    color: #bbb;
    margin-left: 10px;

    &:hover {
      color: white;
    }
  }
`;

export const NavigationBar = () => (
  <Styles>
    <Navbar expand="lg">
      <Navbar.Brand>
        {" "}
        <p class="h2 navbar-style"> Music Horizon 2021</p>{" "}
      </Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="ms-auto navbar-style">
          <Nav.Item>
            <Nav.Link href="/tours/#/"> Home </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link href="/tours/#/about"> Events </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link href="/tours/#/tickets"> Get your tickets now! </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <form>
              <label htmlFor="header-search">
                <span className="visually-hidden">Search Concerts</span>
              </label>
              <input
                type="text"
                id="header-search"
                placeholder="Search by artist, event or city"
              />
              <button>Search Now!</button>
            </form>
          </Nav.Item>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  </Styles>
);
