import React from "react";
import { Header } from "./main";
//import { Body } from "./main";
import { Footer } from "./main";
import concert from "../images/concert1.jpg";
//import { Link } from "react-router-dom";

export function Home() {
  return (
    <div class="container home">
      <Header />

      <div class="home-content">
        <div>
          <img alt="concert" src={concert} height="500" />
        </div>
        <div class="container p-3 my-3 bg-dark text-white tables">
          <div class="row">
            <div class="col">Upcoming Events</div>
            <div class="col">Dates</div>
            <div class="col"></div>
          </div>
          <div class="row">
            <div class="col">
              <a href="#/about">Iron Filings and a Compass</a>
            </div>
            <div class="col">
              <a href="#/about">August 10</a>
            </div>
            <div class="col">
              <a href="#/tickets">See Tickets</a>
            </div>
          </div>
          <div class="row">
            <div class="col">
              <a href="/#/about">No Rest For the Rappers</a>
            </div>
            <div class="col">
              <a href="/#/about">September 20</a>
            </div>
            <div class="col">
              <a href="/#/tickets">See Tickets</a>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
}
